import { Component, OnInit } from '@angular/core';
import { RestService } from '../provider/rest.service';
import { NavController, AlertController } from '@ionic/angular';

@Component({
  selector: 'app-forgotpass',
  templateUrl: './forgotpass.page.html',
  styleUrls: ['./forgotpass.page.scss'],
})
export class ForgotpassPage implements OnInit {
  email: any;
  showOtp: boolean = false;
  otp: any;
  showPass: boolean = false;
  newPass: any;
  apiResponse: any;
  constructor(private RestService: RestService, public navCtrl: NavController, public alertController: AlertController) { }

  ngOnInit() {
    //this.email=JSON.parse(localStorage.userData).UserName;
  }

  async getOtp() {
    if (this.email == '' || this.email == undefined) {
      const alert = await this.alertController.create({
        cssClass:'cstmAlert',
        message: 'Please Enter Email.',
        buttons: ['OK']
      });
      await alert.present();
      return;
    }
    var data = { username: this.email };
    this.RestService.postServices('Logins/ForgotPasswordStep1', data).then(async (response) => {
      console.log(response);
      this.apiResponse = response;
      if (this.apiResponse.Message == "success") {
        //alert(response.MessageDescription);
        this.showOtp = true;
        const alert = await this.alertController.create({
          header: '',
          message: this.apiResponse.MessageDescription,
          cssClass: 'secondary',
          buttons: [
            {
              text: 'Ok',
              role: 'ok',
              handler: (blah) => {
                console.log('Confirm Cancel: blah');
              }
            }
          ]
        });
  
        await alert.present();
      } else {
        const alert = await this.alertController.create({
          header: '',
          message: this.apiResponse.MessageDescription,
          cssClass: 'required',
          buttons: [
            {
              text: 'Ok',
              role: 'ok',
              handler: (blah) => {
                console.log('Confirm Cancel: blah');
              }
            }
          ]
        });
  
        await alert.present();
      }
     
      // alert(this.apiResponse.MessageDescription);
    });
  }

  async submitOtp() {
    if (this.email == '' || this.email == undefined) {
      const alert = await this.alertController.create({
        cssClass:'cstmAlert',
        message: 'Please Enter Email.',
        buttons: ['OK']
      });
      await alert.present();
      return;
    }

    if (this.otp == '' || this.otp == undefined) {
      const alert = await this.alertController.create({
        cssClass:'cstmAlert',
        message: 'Please Enter OTP.',
        buttons: ['OK']
      });
      await alert.present();
      return;
    }
    var data = { username: this.email, otp: this.otp };
    this.RestService.postServices('Logins/ForgotPasswordStep2', data).then(async (response) => {
      console.log(response);
      this.apiResponse = response;
      if (this.apiResponse.Message == "success") {
        // alert(response.MessageDescription);
        this.showPass = true;
        this.showOtp = false;
        const alert = await this.alertController.create({
          header: '',
          message: this.apiResponse.MessageDescription,
          cssClass: 'secondary',
          buttons: [
            {
              text: 'Ok',
              role: 'ok',
              handler: (blah) => {
                console.log('Confirm Cancel: blah');
              }
            }
          ]
        });
  
        await alert.present();
      } else {
        const alert = await this.alertController.create({
          header: '',
          message: this.apiResponse.MessageDescription,
          cssClass: 'required',
          buttons: [
            {
              text: 'Ok',
              role: 'ok',
              handler: (blah) => {
                console.log('Confirm Cancel: blah');
              }
            }
          ]
        });
  
        await alert.present();
      }
      
      // alert(this.apiResponse.MessageDescription);
    });
  }

  async submitNewPassword() {
    if (this.email == '' || this.email == undefined) {
      const alert = await this.alertController.create({
        cssClass:'cstmAlert',
        message: 'Please Enter Email.',
        buttons: ['OK']
      });
      await alert.present();
      return;
    }

    if (this.newPass == '' || this.newPass == undefined) {
      const alert = await this.alertController.create({
        cssClass:'cstmAlert',
        message: 'Please Enter New Password.',
        buttons: ['OK']
      });
      await alert.present();
      return;
    }
    
    var data = { username: this.email, newPassword: this.newPass };
    this.RestService.postServices('Logins/ForgotPasswordStep3', data).then(async (response) => {
      console.log(response);
      this.apiResponse = response;
      if (this.apiResponse.Message == "success") {
        this.navCtrl.navigateRoot("/login");
        const alert = await this.alertController.create({
          header: '',
          message: this.apiResponse.MessageDescription,
          cssClass: 'secondary',
          buttons: [
            {
              text: 'Ok',
              role: 'ok',
              handler: (blah) => {
                console.log('Confirm Cancel: blah');
              }
            }
          ]
        });
  
        await alert.present();
      } else {
        const alert = await this.alertController.create({
          header: '',
          message: this.apiResponse.MessageDescription,
          cssClass: 'required',
          buttons: [
            {
              text: 'Ok',
              role: 'ok',
              handler: (blah) => {
                console.log('Confirm Cancel: blah');
              }
            }
          ]
        });
  
        await alert.present();
      }
     
      // alert(this.apiResponse.MessageDescription);
    });
  }

}
